.bg-modal {
  width: 100%;
  height: auto;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center; }
  .bg-modal h1, .bg-modal h3 {
    color: white;
    font-weight: 400; }
  .bg-modal ul {
    list-style: none; }
  .bg-modal li {
    display: inline;
    padding: 6px; }
  .bg-modal li a {
    text-decoration: none;
    color: black; }
  .bg-modal li a:hover {
    text-decoration: underline; }
  .bg-modal .button {
    background-color: #1686d9;
    border: 2px solid white;
    border-radius: 8px;
    text-decoration: none;
    padding: 10px 28px;
    color: white;
    margin-top: 10px; }
  .bg-modal .button:hover {
    background-color: white;
    color: #1686d9;
    border: 2px solid #1686d9; }
  .bg-modal .modal-contents {
    height: auto;
    width: 100%;
    background-color: white;
    text-align: center;
    padding: 20px;
    position: relative;
    border-radius: 4px;
    height: 650px;
    overflow: hidden;
    border: 1px solid #c8c8c8; }
  .bg-modal .modal-contents p {
    font-size: 20px;
    font-weight: bold;
    background-color: #2185d0;
    color: white;
    display: inline-block;
    clear: both;
    border-radius: 5px;
    padding: 10px; }
  .bg-modal .currWrap {
    background-color: black; }
  .bg-modal input {
    margin: 15px auto;
    display: block;
    width: 100%;
    padding: 8px;
    border: none;
    border-bottom: 1px solid gray;
    font-size: 25px;
    text-align: center; }
  .bg-modal .close {
    position: absolute;
    top: 0;
    right: 10px;
    font-size: 42px;
    color: #333;
    -webkit-transform: rotate(45deg);
    transform: rotate(45deg);
    cursor: pointer; }
  .bg-modal .close:hover {
    color: #666; }
  .bg-modal .dropbtn {
    background-color: #4CAF50;
    color: white;
    padding: 16px;
    font-size: 16px;
    border: none;
    cursor: pointer; }
  .bg-modal .dropbtn:hover, .bg-modal .dropbtn:focus {
    background-color: #3e8e41; }
  .bg-modal .dropdown {
    margin-left: 50%;
    margin-top: 30px;
    position: relative;
    display: inline-block; }
  .bg-modal .dropdown-content {
    display: none;
    position: absolute;
    background-color: #f1f1f1;
    min-width: 160px;
    overflow: auto;
    box-shadow: 0px 8px 16px 0px rgba(0, 0, 0, 0.2);
    right: 0;
    z-index: 1; }
  .bg-modal .dropdown-content a {
    color: black;
    padding: 12px 16px;
    text-decoration: none;
    display: block; }
  .bg-modal .dropdown a:hover {
    background-color: #ddd; }
  .bg-modal .show {
    display: block; }

